import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'churchforms',
  templateUrl: './churchforms.component.html',
  styleUrls: ['./churchforms.component.scss']
})
export class ChurchformsComponent implements OnInit {

  constructor() { }

  ufname  = '';
  uffname = '';
  uflname = '';
  umfname = '';
  umlname = '';
  uemname = '';
  uconame = '';
  uworkaddress = '';
  countyRFather = '';
  countyRMother = '';
  
  
  uPekee = false;
  uMarried = false;
  uDivorced = false;
  uMarriedOther = false;
  
  getUserData(event){
	switch(this.ufname){
		case 'Daniel': {
			this.uffname = 'Charles';
			this.uflname = 'Mwita';
			this.umfname = 'Madimweri';
			this.umlname = 'Temba';
			this.countyRFather = 'Tanzania';
			this.countyRMother = 'Tanzania';
			this.uworkaddress  = '25 Ohio street, plot no 45, Dar Es Salaam, TZ';
			this.uemname  = 'Wizara ya Fedha';
			this.uconame  = 'None';
			
			//this.
			//reset fields.
			this.uPekee = false;
			this.uMarried = false;
			this.uDivorced = false;
			this.uMarriedOther = false;
			
			this.uPekee = true;
		   break;
		}
		
		case 'Alfred': {
			this.uffname = 'Leonard';
			this.uflname = 'Mpendwa';
			this.umfname = 'Joyce';
			this.umlname = 'Mayala';
			this.countyRFather = 'Tanzania';
			this.countyRMother = 'Tanzania';
  			this.uworkaddress  = '152 Wamakula street, plot no 15, Ruvuma, TZ';
			this.uemname  = 'Hamshauri ya mkoa wa Ruvuma';
			this.uconame  = 'None';
			
			//reset fields.
			this.uPekee = false;
			this.uMarried = false;
			this.uDivorced = false;
			this.uMarriedOther = false;
			
			this.uMarried = true;
		   break;
		}
	}// switch.
	
	
  }//get UserData. 
  
  
  
  ngOnInit() {
    
  }

}



