import { NgModule } from '@angular/core';

import { ThemeModule } from '../../@theme/theme.module';
import { FormsRoutingModule, routedComponents } from './forms-routing.module';
import { ChurchformsComponent } from './churchforms/churchforms.component';
import { ChamaformsComponent } from './chamaforms/chamaforms.component';
import { TrucktrackformsComponent } from './trucktrackforms/trucktrackforms.component';
import { MedicalformsComponent } from './medicalforms/medicalforms.component';

@NgModule({
  imports: [
    ThemeModule,
    FormsRoutingModule,
  ],
  declarations: [
    ...routedComponents,
    ChurchformsComponent,
    ChamaformsComponent,
    TrucktrackformsComponent,
    MedicalformsComponent,
  ],
})
export class FormsModule { }



