import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FormsComponent } from './forms.component';
import { FormInputsComponent } from './form-inputs/form-inputs.component';
import { FormLayoutsComponent } from './form-layouts/form-layouts.component';
import { ChurchformsComponent } from './churchforms/churchforms.component';
import { ChamaformsComponent } from './chamaforms/chamaforms.component';
import { TrucktrackformsComponent } from './trucktrackforms/trucktrackforms.component';
import { MedicalformsComponent } from './medicalforms/medicalforms.component';

const routes: Routes = [{
  path: '',
  component: FormsComponent,
  children: [{
    path: 'inputs',
    component: FormInputsComponent,
  },{
    path: 'layouts',
    component: FormLayoutsComponent,
  },{
    path: 'churchforms',
    component: ChurchformsComponent,
  },{
    path: 'chamaforms',
    component: ChamaformsComponent,
  },{
    path: 'trucktrackforms',
    component: TrucktrackformsComponent,
  },{
    path: 'medicalforms',
    component: MedicalformsComponent,
  }],
}];

@NgModule({
  imports: [
    RouterModule.forChild(routes),
  ],
  exports: [
    RouterModule,
  ],
})
export class FormsRoutingModule {

}

export const routedComponents = [
  FormsComponent,
  FormInputsComponent,
  FormLayoutsComponent,
  ChurchformsComponent,
];



