import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'chamaforms',
  templateUrl: './chamaforms.component.html',
  styleUrls: ['./chamaforms.component.scss']
})
export class ChamaformsComponent implements OnInit {

  constructor() { }

  ufname  = '';
  ulname  = '';
  umname  = '';
  udob    = '';
  ulocOfB = '';
  uffname = '';
  uflname = '';
  umfname = '';
  umlname = '';
  uemname = '';
  uconame = '';
  uAccountNo = 'N/A';
  uworkaddress = '';
  countyRFather = '';
  countyRMother = '';
  uMarried = false;
  uDivorced = false;
  uMarriedOther = false;
  uPekee = false;
  
  getSearch(term){
	switch(term){
		case 'Daniel': {
		    this.ufname  = 'Daniel';
			this.ulname  = 'Magesa';
			this.umname  = 'Benedict';
			this.udob    = '20/05/1985';
			this.ulocOfB = 'Pemba';
			this.uffname = 'Charles';
			this.uflname = 'Mwita';
			this.umfname = 'Madimweri';
			this.umlname = 'Temba';
			this.uAccountNo = '1545487896';
			this.countyRFather = 'Tanzania';
			this.countyRMother = 'Tanzania';
			this.uworkaddress  = '25 Ohio street, plot no 45, Dar Es Salaam, TZ';
			this.uemname  = 'Wizara ya Fedha';
			this.uconame  = 'None';
			
			this.uPekee = false;
			this.uMarried = false;
			this.uDivorced = false;
			this.uMarriedOther = false;
			
			this.uPekee = true;
		   break;
		}
		
		case 'Alfred': {
		  this.ufname  = 'Alfred';
			this.ulname  = 'Mashaka';
			this.umname  = 'Mweri';
			this.uAccountNo = '1565455687';
			this.udob    = '28/06/1988';
			this.ulocOfB = 'Dar es Salaam';
			this.uffname = 'Leonard';
			this.uflname = 'Mpendwa';
			this.umfname = 'Joyce';
			this.umlname = 'Mayala';
			this.countyRFather = 'Tanzania';
			this.countyRMother = 'Tanzania';
  			this.uworkaddress  = '152 Wamakula street, plot no 15, Ruvuma, TZ';
			this.uemname  = 'Hamshauri ya mkoa wa Ruvuma';
			this.uconame  = 'None';
			
			this.uPekee = false;
			this.uMarried = false;
			this.uDivorced = false;
			this.uMarriedOther = false;
			
			this.uMarried = true;
		   break;
		}
	}//switch
  }
  ngOnInit() {
  }
}


